const Contacts = ({ contacts }) => {
  console.log(contacts);
  return (
    <div>
      <center>
        <h1>Card List</h1>
      </center>
      {contacts.map(contact => (
        <div>
          <div>
            <a href={Rename(contact.name)}>{contact.name}</a>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Contacts;
